import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { Swiper, SwiperSlide } from "swiper/react"
import { Autoplay } from "swiper/modules"
import 'swiper/css';
import 'swiper/css/pagination';

const TopSlider = ({
  preliminary
}: {
  preliminary?: boolean 
}) => {
  const [isRendered, setIsRendered] = React.useState(false)
  const data = useStaticQuery(graphql`
    query {
      allGamesJson {
        edges {
          node {
            name
            spUrl
            blank
            slideShowImage {
              childImageSharp {
                gatsbyImageData
              } 
            }
          }
        }
      }
    }
  `)

  const edges = data.allGamesJson.edges
  
  React.useEffect(() => {
    setIsRendered(true)
  })

  return isRendered ? (
    <>
      <div className="slider-container">
        <Swiper
          className="slider-inner"
          spaceBetween={-2}
          slidesPerView={3}
          autoplay={true}
          loop={true}
          pagination={{
            clickable: true,
          }}
        >
          { edges.map((edge, i) => {
              return (
                <React.Fragment key={i}>
                  {
                    edge.node.spUrl ? (
                      edge.node.blank ?
                        <SwiperSlide>
                          <a href={edge.node.spUrl} target="_blank">
                            <GatsbyImage
                              image={getImage(edge.node.slideShowImage)!}
                              alt=""
                            />
                          </a> 
                        </SwiperSlide>
                      :
                        <SwiperSlide>
                          <a href={edge.node.spUrl}>
                            <GatsbyImage
                              image={getImage(edge.node.slideShowImage)!}
                              alt=""
                            />
                          </a> 
                        </SwiperSlide>
                    )
                  :
                    <SwiperSlide>
                      <GatsbyImage
                        image={getImage(edge.node.slideShowImage)!}
                        alt=""
                      />
                    </SwiperSlide>
                  }
                </React.Fragment>
              )
            })
          }
        </Swiper>
      </div>
    </>
  ) : (
    <></>
  )
}

export default TopSlider 