import * as React from "react"
import { graphql, useStaticQuery } from "gatsby"

const Topics = ({
   slice 
}: {
    slice?: number
}) => {
    const data = useStaticQuery(graphql`
        query {
            allTopicsJson {
                edges {
                    node {
                        title
                        target
                        date
                    }
                }
            }
        }
    `);
    
    const edges = !!slice ? data.allTopicsJson.edges.slice(0, slice) : data.allTopicsJson.edges.slice(0, 1);
    return (
        <>
            <div className="information-item topics-container">
                <div className="caption">
                    <p className="caption-text">NEWS</p>
                </div>
                <div className="topics-inner">
                    { edges.map((edge, i) => {
                            return (
                                <React.Fragment key={i}>
                                    <div className="topics-item-outer">
                                        <div className="topics-item date"><p>{edge.node.date}</p></div>
                                        <div className="topics-item title">
                                            {
                                                edge.node.target != "" ?
                                                    <p><a href={edge.node.target} target="_blank">{edge.node.title}</a></p>
                                                    :
                                                    <p>{edge.node.title}</p>
                                            }
                                        </div>
                                    </div>
                                </React.Fragment>
                            )
                        })
                    }
                </div>
            </div>
        </>
    )
}

export default Topics